
/**
 * 创建一个字典对象
 * @param obj
 * @description 字典对象不具有 object 原型方法，这里主要处理一些特殊的保留字处理问题
 * @fix https://work.aone.alibaba-inc.com/issue/31901608
 */
export function createDict<T extends {}>(obj: T): T {
  const dict = Object.create(null);
  for (const key in obj) {
    if (Object.prototype.hasOwnProperty.call(obj, key)) {
      const value = obj[key];
      dict[key] = value;
    }
  }
  return dict;
}
